.page {
    opacity: .5;
    transform: scale(1.5);
    display: flex;
    flex-wrap: wrap;
    background: var(--themeBlack);
    min-height: 100vh;
    transition: .5s;
}

.page-left {
    width: 50%;
}

.page-right {
    width: 50%;
    padding: 100px;
    position: relative;
    display: flex;
    align-items: center;
}
.wrp-inner {
    width: 100%;
}
.page-left {
    position: relative;
    padding: 50px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.left-page-wrp .logo {
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 3;
}

.left-page-wrp  a.closePage {
    position: absolute;
    top: 70px;
    right: -10px;
    border-bottom: 2px solid;
    text-transform: uppercase;
    font-weight: 500;
    display: flex;
    align-items: center;
    transform: rotate(-90deg);
    font-family: var(--headingFont);
    font-size: 22px;
    z-index: 2;
    padding-bottom: 5px;
}

.left-page-wrp a.closePage img {
    margin-left: 5px;
}

.leftback {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.leftback:after {content: "";position: absolute;width: 100%;height: 100%;background: #000;top: 0;left: 0;opacity: .4;}

.leftback img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: zooms infinite 80s;
} 
@keyframes zooms{
    0%{
        transform: scale(1);
    }  
    50%{
        transform: scale(1.5);
    }
    100%{
        transform: scale(1);
    }
}
.page-left-contents {
    text-align: center;
} 
.numDetails {
    display: flex;
    justify-content: space-between;
} 
.numBlk {
    text-align: center;
    margin-top: 60px;
    width: 40%;
}
  

ul.blists li {
    display: block;
    margin-top: 60px;
}

ul.blists li a {
    display: block;
    transition: .3s;
}

ul.blists li a:hover {
    color: #A37B7F;
}

ul.blists {
    margin-top: 80px;
}

.blists h1 {
    font-size: 96px;
    line-height: 110px;
    margin: 0;
} 
ul.blists.mdlist {
    margin: 0;
}

ul.blists.mdlist li h1 {
    font-size: 64px;
    line-height: 80px;
}
a.closeSubPage {
    position: absolute;
    right: 40px;
    top: 40px;
    background: var(--themeWhite);
    width: 40px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    z-index: 99;
}

a.closeSubPage img {
    filter: invert(1);
}
.page-left-contents a.theme-btn {
    margin: auto;
    margin-top: 30px;
}
ul.blists.xslist li h1 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 15px;
}
ul.rulls li {
    list-style: circle;
    margin-left: 50px;
}

ul.rulls {
    margin-bottom: 20px;
}

.page-bottom-menu {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
}

.page-bottom-menu a {
    margin: 0px 20px;
    background: transparent;
    display: inline-block; 
    transition: .3s;
    position: relative;
}
.page-bottom-menu a.lastMenu:after {
    content: "|";
    position: absolute;
    left: -20px;
    top: 50%;
    transform: translateY(-50%);
    color: white;
    transition: .2s;
}
.page-bottom-menu a:hover {
    text-decoration: underline;
}



@media (min-width:992px) and (max-width:1200px){
    .page-right { 
        padding: 50px;
        padding-bottom: 100px;
    }
    .numBlk {  
        width: 45%;
    }
    .blists h1 {
        font-size: 72px;
        line-height: 80px; 
    } 
    ul.blists.mdlist li h1 {
        font-size: 50px;
        line-height: 64px;
    }




}
  
@media (min-width:768px) and (max-width:991px){ 
    .numBlk {  
        width: 50%;
    }
    .page-right {
        width: 100%;
        padding: 35px;
        padding-top: 50px;
        padding-bottom: 100px;
    }
    .page-left {
        width: 100%;
        padding-top: 200px;
        padding-bottom: 100px;
    }
    ul.blists li { 
        margin-top: 30px;
    } 
    .blists h1 {
        font-size: 72px;
        line-height: 80px; 
    } 
    ul.blists { 
        text-align: center;
    }
    ul.blists.mdlist li h1 {
        font-size: 50px;
        line-height: 64px;
    }
    a.closeSubPage { 
        right: 30px;
        top: 65px; 
    }



} 

@media (max-width:767px){ 
    .numBlk {  
        width: 50%;
    }
    .page-right {
        width: 100%;
        padding: 20px;
        padding-top: 50px;
        padding-bottom: 100px;
    }
    .page-left {
        width: 100%;
        padding-top: 180px;
        padding-bottom: 70px;
        padding-left: 10px;
        padding-right: 10px;
    }
    .numBlk h5 {
        font-size: 18px;
        line-height: 16px;
    }
    ul.blists li { 
        margin-top: 30px;
    } 
    .blists h1 {
        font-size: 48px;
        line-height: 55px;
    }
    ul.blists { 
        text-align: center; 
    }
    ul.blists.xslist {  
        margin-top: 0px;
    }
    ul.blists.mdlist li h1 {
        font-size: 36px;
        line-height: 50px;
    }
    a.closeSubPage { 
        right: 30px;
        top: 65px; 
    }




}











