form.loginform {
    width: 100%;
    margin-top: 40px;
}

form.loginform label {
    width: 100%;
    text-align: left;
    position: relative;
}

form.loginform label img {
    position: absolute;
    right: 0;
    top: 18px;
}

form.loginform label input {
    height: 35px; 
    padding-right: 20px;
}
form.loginform label.ckb {
    margin-top: 10px;
    margin-bottom: 30px;
}
form.loginform  label.ckb {
    margin-top: 10px;
    margin-bottom: 30px;
}

form.loginform  button.theme-btn {
    margin: auto;
    margin-bottom: 30px;
}

form.loginform p {
    text-align: center;
    margin-bottom: 5px;
    font-size: 16px;
}

form.loginform p a {
    text-decoration: underline;
}
form.loginform .formBottom label {text-align: center;margin-bottom: 10px;}

form.loginform .formBottom {
    margin-top: 30px;
}

form.loginform .formBottom button.theme-btn {
    margin-top: 50px;
}
form.loginform .inplabel label {
    width: 48%;
}
form.loginform.newslatter {
    max-width: 500px;
    margin: auto;
}

form.loginform.newslatter label {
    margin-bottom: 20px;
    margin-top: 50px;
}
form.loginform h5 {
    text-align: center;
}
form.loginform .inplabel label img {
    top: 18px;
}
form.loginform label img {
    position: absolute;
    right: 0;
    top: 12px;
}
form.loginform.maillogin {
    max-width: 500px;
    margin: auto;
}

form.loginform.maillogin label {
    margin-top: 15px;
}

form.loginform label img.pass {
    filter: invert(1);
    width: 18px;
    top: 8px;
}
form.loginform.signup label img.pass {
    top: 16px;
}






