:root{
    --themeWhite: #DCDCDC;
    --themeBlack: #0E1015;
    --bodyFont: 'Barlow', sans-serif;
    --headingFont: 'Fjalla One', sans-serif;
}


body { 
    font-family: var(--bodyFont);
    color: var(--themeWhite);
    background: var(--themeBlack);
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    overflow-x: hidden;
    cursor: auto;
  } 
  button {
    cursor: pointer; } 
  a:focus,
  .button:focus {
    text-decoration: none;
    outline: none; }
  
  a:focus,
  a:hover {
    color: inherit;
    text-decoration: none; }
  
  a,
  button {
    color: inherit;
    outline: medium none; }
  
  button:focus, input:focus, input:focus, textarea, textarea:focus {
    outline: 0; }
  
   
h1,
h2,
h3,
h4,
h5,
h6 { 
    font-family: var(--headingFont);
    color: var(--themeWhite);
    font-weight: 500;
    margin-top: 0px;  
}
  
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit; 
}
h1 {
    font-size: 120px;
    text-transform: uppercase;
    line-height: 140.98px;
}
h2 {
font-size: 35px; 
}

h3 {
font-size: 28px; 
}

h4 {
font-size: 22px; 
}

h5 {
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
}


ul {
    margin: 0px;
    padding: 0px; 
}

li {
    list-style: none; 
} 
a{
    text-decoration: none;
} 
a.underline {
    text-decoration: underline;
} 
.theme-btn {
    display: block;
    font-weight: 600;
    width: max-content;
    color: var(--themeBlack);
    text-transform: uppercase;
    background: var(--themeWhite);
    padding: 15px 40px;
    border-radius: 3px;
    transition: .3s;
    outline: none;
    border: none;
}

.theme-btn:hover {
    background: #fff;
}

input {
    display: block;
    width: 100%; 
    padding-right: 0px;
    border: none;
    background: none;
    border-bottom: 2px solid  var(--themeWhite);
    color:  var(--themeWhite);
    font-size: 16px;
}

label, ::placeholder {
    color:  var(--themeWhite);
    text-transform: uppercase;
    font-weight: 500;
    font-family: var(--headingFont);
}
textarea {
    color:  var(--themeWhite);
    width: 100%;
    height: 100px;
    border: none;
    border-bottom: 2px solid  var(--themeWhite);
    background: no-repeat;
    font-size: 16px; 
    margin-bottom: 0 !important;
}
::-webkit-scrollbar{
    width: 0;
}






.ckb {
    display: block;
    position: relative; 
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: center;
  } 
  .ckb input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  } 
  .checkmark {
    position: relative;
    display: inline-block;
    top: 3px;
    left: 0;
    height: 20px;
    width: 20px;
    border: 2px solid var(--themeWhite);
    border-radius: 2px;
    margin-right: 10px;
}
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  } 
  .ckb input:checked ~ .checkmark:after {
    display: block;
  } 
  .ckb .checkmark:after {
    left: 6px;
    top: 2px;
    width: 5px;
    height: 9px;
    border: solid var(--themeWhite);
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}
.react-datepicker__day{
    outline: none !important;
}













.passPro {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s;
}
.passes input#passtest {
    width: 300px;
    margin-bottom: 20px;
}

.passes  button.theme-btn {
    font-size: 16px;
    padding: 10px 30px;
    margin: auto;
}
body.openPlease .passPro {
    opacity: 0;
    visibility: hidden;
}













 

