    h1 {
        font-size: 70px;
        line-height: 95.98px;
    }
}
@media (min-width:768px) and (max-width:991px){ 
    h5 {
        font-size: 20px;
        line-height: 24px; 
    }
    h1 {
        font-size: 82px; 
        line-height: 95.98px;
    }
}

@media (max-width:767px){ 
    h5 {
        font-size: 20px;
        line-height: 24px; 
    }
    h1 {
        margin-bottom: 0;
        font-size: 50px;
        line-height: 60px;
    }
    body{
        font-size: 16px;
        line-height: 20px;
    }


}