.simpleSelectFormContainer {
  width: 100%;
  height: 100%;
  background: none;
  color: #dcdcdc;
  border-bottom: 2px solid #dcdcdc;
  text-transform: uppercase;
  font-size: 16px;
}

.simpleSelectForm {
  width: 100%;
  height: 100%;
  background: none;
  color: rgb(220, 220, 220);
  border: none;
  text-transform: uppercase;
}

.simpleSelectForm:active,
.simpleSelectForm:focus,
.simpleSelectForm:focus-visible {
  outline: none;
}

.simpleSelectFormOption {
  background-color: #ecebeb;
  color: black;
}
