.home {
    text-align: center;
    height: 100vh;
    min-height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    padding-top: 70px;
    transition: .5s;
    opacity: .5;
    transform: scale(.8);
    z-index: 2;
    position: relative;
    overflow: hidden;
}
.home img.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1; 
    animation: backEff infinite 120s;
} 
@keyframes backEff{
    0%{
        transform: scale(1);
    }  
    50%{
        transform: scale(1.5);
    }
    100%{
        transform: scale(1);
    }
}



.home:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    top: 0;
    z-index: -1;
    left: 0;
    opacity: .4;
}

.menu {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
}

.menu ul li {
    display: block;
}

.menu ul li a {
    display: block;
    padding: 25px 90px;
    background: var(--themeBlack);
    margin-bottom: 15px;
    text-align: center;
    text-transform: uppercase;
    transition: .3s;
    font-weight: 400;
    border-radius: 3px;
    color: var(--themeWhite);
    font-family: var(--headingFont);
    font-size: 24px;
}

.menu ul li a:hover {
    background: var(--themeWhite);
    color: var(--themeBlack); 
} 
.home a.logo {
    position: absolute;
    top: 33px;
    left: 60px;
}
button.menuIcon {
    position: absolute;
    width: 40px;
    border: none;
    background: none;
    outline: none;
    top: 75px;
    right: 60px;
    padding-bottom: 20px;
    display: none;
}
button.menuIcon span {
    width: 100%;
    height: 4px;
    background: var(--themeWhite);
    display: block;
    position: relative;
    transition: .3s;
} 
button.menuIcon span:after {content: "";position: absolute;width: 60%;height: 100%;background: var(--themeWhite);bottom: -20px;right: 0;transition: .3s;}
button.menuIcon span:before {content: "";position: absolute;width: 85%;height: 100%;background: var(--themeWhite);bottom: -10px;right: 0;transition: .3s;}
button.menuIcon:hover span:before, 
button.menuIcon:hover span:after {
    width: 100%;
}
button.menuIcon.humbargerAni {
    z-index: 4;
} 
button.menuIcon.humbargerAni span:before {
    transform: rotate(45deg);
    width: 100%;
    top: 0;
} 
button.menuIcon.humbargerAni span:after {
    transform: rotate(-45deg);
    top: 0;
    left: 0;
    width: 100%;
} 
button.menuIcon.humbargerAni span {
    background: none;
} 
.home nav {
    position: absolute;
    bottom: 15px;
}

.home nav ul li {
    display: inline-block;
}

.home nav ul li a {
    display: inline-block;
    margin: 0 10px;
    transition: .3s;
    padding: 10px;
    padding-top: 7px;
    border-radius: 3px;
}

.home nav ul li a:hover {
    background: var(--themeWhite);
    color: var(--themeBlack);
}




@media (min-width: 992px) and (max-width:1200px){    
    .home-wrp h1 {
        font-size: 90px;
        line-height: initial;
    }
    .menu ul li a { 
        padding: 15px 40px;  
        border-radius: 2px; 
        font-size: 21px;
    }
    
}


@media (min-width: 768px) and (max-width:991px){    
    .home-wrp h1 {
        font-size: 90px;
        line-height: initial;
    }
    .menu ul li a { 
        padding: 15px 40px;  
        border-radius: 2px; 
        font-size: 21px;
    }
    button.menuIcon {
        display: block;
    }
    .menu {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        transform: translateY(0);
        background: var(--themeBlack);
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
    }
    .menu ul li a{
        border: 1px solid var(--themeWhite);
    } 
    .menu.menuShow{
        opacity:1;
        visibility: visible;
    }
    .home { 
        min-height: 600px; 
    }

}


@media (max-width:767px){ 
    .home a.logo { 
        top: 30px;
        left: 15px;
    }  
    .home-wrp h1 {
        font-size: 68px;
        line-height: initial;
        padding: 0 30px;
    }
    .home-wrp h5 {
        margin: 0;
        font-size: 20px;
    }
    .menu ul li a { 
        padding: 15px 40px;  
        border-radius: 2px; 
        font-size: 21px;
    }
    button.menuIcon {
        display: block;
        width: 40px; 
        top: 65px;
        right: 15px; 
    }
    .menu {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        transform: translateY(0);
        background: var(--themeBlack);
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
    }
    .menu ul li a{
        border: 1px solid var(--themeWhite);
    } 
    .menu.menuShow{
        opacity:1;
        visibility: visible;
    }
    .home nav ul li a {
        margin: 0 5px;
    }
    .home { 
        min-height: 600px; 
    }



}