.ckb {
    display: block;
    position: relative; 
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: center;
  } 
  .ckb input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  } 
  .checkmark {
    position: relative;
    display: inline-block;
    top: 3px;
    left: 0;
    height: 20px;
    width: 20px;
    border: 2px solid #DCDCDC;
    border: 2px solid var(--themeWhite);
    border-radius: 2px;
    margin-right: 10px;
}
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  } 
  .ckb input:checked ~ .checkmark:after {
    display: block;
  } 
  .ckb .checkmark:after {
    left: 6px;
    top: 2px;
    width: 5px;
    height: 9px;
    border: solid #DCDCDC;
    border: solid var(--themeWhite);
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
