    z-index: 4;
} 
button.menuIcon.humbargerAni span:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 100%;
    top: 0;
} 
button.menuIcon.humbargerAni span:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top: 0;
    left: 0;
    width: 100%;
} 
