.formPages {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: 30px;
    margin-bottom: 15px;
}
.formPages a {
     opacity: .3;
}
.formPages a:hover,
.formPages a.active{
    color: var(--themeBlack);
    opacity: 1;
    background: #fff;
}
.inplabel {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.inplabel label {
    width: 48%;
    position: relative;
    margin-top: 20px;
}

.inplabel label img {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    transition: .3s;
}
label.selectActive img {
    transform: rotate(179deg);
    top: 12px;
}
.formBottom label {
    width: 100%;
}


label.ckb { 
    font-size: 16px;
}
.formBottom button {
    margin: auto;
    margin-top: 40px;
    margin-bottom: 30px;
}

.formBottom p {
    text-align: center;
    margin: 0;
    font-size: 16px;
} 
.inplabel.msgbx {
    margin-top: 10px;
    margin-bottom: 50px;
}
 
.inplabel.msgbx label {
    width: 100%;
}

.inplabel.msgbx label img {
    transform: unset !important;
    top: auto;
    bottom: 15px;
}
  
.selectit {
    width: 100%;
    position: relative;
    height: 30px;
}

.selectit ul {
    position: absolute;
    top: 40px;
    left: 0;
    background: var(--themeWhite);
    z-index: 99;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}
label.selectActive ul {
    opacity: 1;
    visibility: visible;
    top: 34px;
}
.selectit ul li {
    display: block;
    cursor: pointer;
    padding: 5px 10px;
    text-transform: capitalize;
    transition: .3s;
    color: var(--themeBlack);
    text-transform: uppercase;
    font-size: 16px;
    opacity: .5;
    font-weight: 600;
}
.selectit ul li:hover,.selectit ul li.active {
    opacity: 1;
}

.selectit ul li:hover {
    background: var(--themeWhite);
}
.selectit span.showing {
    display: block;
    width: 100%;
    padding-bottom: 5px;
    border-bottom: 2px solid var(--themeWhite);
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    padding-top: 5px;
}

label.disabled {
    opacity: .5;
    pointer-events: none;
}
 

.autos {
    position: absolute;
    top: 42px;
    left: 0;
    width: 100%;
    background: #fff;
    z-index: 3;
    max-height: 200px;
    overflow: auto;
    opacity: 0;
    visibility: hidden;
    transition: .2s;
}
label.autoComp.showAutoComp .autos{
    opacity: 1;
    visibility: visible;
}
label.autoComplate.active .autos{
    opacity: 1;
    visibility: visible;
}
.autos ul li {
    display: block;
    width: 100%;
    padding: 5px 10px;
    transition: .3s;
    cursor: pointer;
    text-transform: capitalize;
    text-align: left;
    color: var(--themeBlack);
    font-size: 16px;
}
.inplabel label input {
    height: 40px;
}

.formBottom label{
    font-family: var(--bodyFont);
}  
.react-datepicker-wrapper { 
    width: 100%;
}
.react-datepicker__header{
    background-color: var(--themeBlack) !important;
}
.react-datepicker__day--keyboard-selected, 
.react-datepicker__month-text--keyboard-selected, 
.react-datepicker__quarter-text--keyboard-selected, 
.react-datepicker__year-text--keyboard-selected {
    background-color: var(--themeBlack) !important;
}
.react-datepicker__current-month {
    color: var(--themeWhite) !important;
}
.react-datepicker__day-names .react-datepicker__day-name {
    color: var(--themeWhite);
}


