ul.dropdown-list {
    position: absolute;
    top: 41px;
    background: var(--themeWhite);
    width: 100%;
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    max-height: 150px;
    overflow: auto;
}
ul.dropdown-list.true{
    opacity: 1;
    visibility: visible;
}
ul.dropdown-list li.option {
    font-size: 15px;
    color: var(--themeBlack);
    padding: 5px 10px;
    line-height: initial;
    cursor: pointer;
}
div#react-autowhatever-1 ul {
    width: 100%;
    background: white;
    max-height: 200px;
    position: absolute;
    left: 0;
    top: 40px;
    overflow: auto;
    z-index: 3;
}

div#react-autowhatever-1 ul li {
    font-size: 15px;
    color: #000;
    padding: 8px 10px;
    line-height: initial;
    cursor: pointer;
}












@media (min-width: 992px) and (max-width: 1400px){
    .formPages a {
        opacity: .3;
        margin-top: 10px;
    }
}

@media (max-width: 767px){
    .inplabel label{
        width: 100%;
        margin-top: 10px;
    }
    textarea { 
        height: 70px; 
    }
    .formPages a {
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
    }

}